import React from "react";
import styled from "styled-components";
import Task from "./task";
import { Droppable, Draggable } from "react-beautiful-dnd";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faEllipsisH } from "@fortawesome/free-solid-svg-icons";

import "./../App.css";

const TaskList = styled.div`
  margin-top: 12px;
  background-color: ${(props) => (props.isDraggingOver ? "#fff" : "inherit")};
  flex-grow: 1;
  min-height: 100px;
`;
const ClonedDnd = styled.div`
  background-color: #f3f4f6 !important;
  border: 1px solid #d0d1d7;
  border-radius: 5px;
`;

const colors = ["red", "yellow", "blue", "green", "indigo", "purple", "pink"];

const column = (props) => {
  return (
    <React.Fragment>
      <Draggable
        draggableId={props.column.id}
        index={props.index}
        last={props.last}
      >
        {(provided) => (
          <div
            className={props.last ? "flex-1 " : "flex-1 mr-2.5"}
            {...provided.draggableProps}
            ref={provided.innerRef}
          >
            <div class="lg:flex lg:items-center lg:justify-between px-1 mb-1">
              <div class="flex-1 min-w-0">
                <div class="mt-1 flex flex-col sm:flex-row sm:flex-wrap sm:mt-0 sm:space-x-3">
                  <div class="mt-2 flex-grow items-center text-sm text-gray-500">
                    <span
                      className={
                        "flex-shrink-0 px-1.5 h-3 w-3 text-black-800 bg-white rounded-sm border capitalize cursor-pointer " +
                        ` border-${
                          colors[props.index % colors.length]
                        }-100 bg-${colors[props.index % colors.length]}-100`
                      }
                      {...provided.dragHandleProps}
                    >
                      {props.column.title}
                    </span>
                  </div>

                  <div class="mt-2 flex items-center text-sm text-gray-500">
                    <FontAwesomeIcon
                      class="flex-shrink-0 h-3 w-3 text-gray-400 hover:text-gray-900 cursor-pointer"
                      icon={faEllipsisH}
                    />
                  </div>
                  <div class="mt-2 flex items-center text-sm text-gray-500">
                    <FontAwesomeIcon
                      class="flex-shrink-0 h-3 w-3 text-gray-400 hover:text-gray-900 cursor-pointer"
                      icon={faPlus}
                    />
                  </div>
                </div>
              </div>
            </div>

            <Droppable
              droppableId={props.column.id}
              type={"task"}
              renderClone={(provided, snapshot, rubric) => (
                <ClonedDnd
                  {...provided.draggableProps}
                  {...provided.dragHandleProps}
                  ref={provided.innerRef}
                ></ClonedDnd>
              )}
            >
              {(provided, snapshot) => (
                <div
                  class="p-2 bg-gray-50"
                  ref={provided.innerRef}
                  {...provided.droppableProps}
                  isDraggingOver={snapshot.isDraggingOver}
                >
                  {props.tasks.map((task, index) => (
                    <Task key={task.id} task={task} index={index} />
                  ))}
                  <div class="mt-2 p-1.5 flex items-center text-sm  hover:bg-gray-100 cursor-pointer">
                    <FontAwesomeIcon
                      class="mr-2.5 mt-1.5 h-3 w-3 text-gray-300  hover:text-gray-300"
                      icon={faPlus}
                    />
                    <span class="mr-2.5 h-3 w-3 text-gray-300  hover:text-gray-300">
                      Add
                    </span>
                  </div>
                  {provided.placeholder}
                </div>
              )}
            </Droppable>
          </div>
        )}
      </Draggable>
    </React.Fragment>
  );
};

export default column;
