import React from "react";
import styled from "styled-components";
import { Draggable } from "react-beautiful-dnd";

const Container = styled.div`
  border-radius: 2px;
  border: 1px solid #d0d1d7;
  border-radius: 5px
  margin: 8px;
  padding-left:10px;
  margin-right:10px;
  margin-bottom: 8px;
  background-color: ${(props) =>
    props.isDragDisabled
      ? "lightgrey"
      : props.isDragging
      ? "lightgreen"
      : "white"};
  display: flex;    
`;

const Handle = styled.div`
  width: 20px;
  height: 20px;
  background-color: orange;
  border-radius: 4px;
  margin-right: 8px;
`;

const TaskTitle = styled.span`
  font-weight: 500;
  color: #484640;
  margin-top: 5px;
  margin-bottom: 5px;
`;

const task = (props) => {
  const isDragDisabled = false;

  return (
    <React.Fragment>
      <Draggable
        draggableId={props.task.id}
        index={props.index}
        isDragDisabled={isDragDisabled}
      >
        {(provided, snapshot) => (
          <div
            class="text-black-800 p-1.5 my-1.5 bg-white rounded-sm bg-white border border-gray-300 hover:bg-gray-100"
            {...provided.dragHandleProps}
            {...provided.draggableProps}
            ref={provided.innerRef}
            isDragging={snapshot.isDragging}
            isDragDisabled={isDragDisabled}
          >
            <span
              class=""
              {...provided.dragHandleProps}
            >
              {props.task.content}
            </span>
          </div>
        )}
      </Draggable>
    </React.Fragment>
  );
};
export default task;
