import React from "react";
import { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";

const CreateBoard = (props) => {
  const [name, setName] = useState();
  const [showModal, setShowModal] = React.useState(false);

  const createBoard = (ev) => {
    props.onChange(name);
    setName("");
    ev.preventDefault();
    setShowModal(false);
  };

  const onNameChange = (ev) => {
    setName(ev.target.value);
  };

  const formInput = (
    <form onSubmit={createBoard} class="flex-grow flex rounded-md shadow-sm">
      <input
        type="text"
        class="mt-1 focus:ring-indigo-500 focus:border-indigo-500 flex-1 block w-full rounded rounded-r-md  border-gray-300 border p-2"
        value={name}
        onChange={onNameChange}
        autocomplete="off"
      />
    </form>
  );

  return (
    <React.Fragment>
      <div class="max-w-7xl mx-auto flex-column flex   p-2 sm:px-6 lg:px-8">
        <div class="flex-grow"></div>
        <button
          type="submit"
          style={{ transition: "all .15s ease" }}
          onClick={() => setShowModal(true)}
          class="inline-flex justify-center py-1 px-4 border border-transparent shadow-sm text-sm font-small rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
        >
          <FontAwesomeIcon
            class="-ml-1 h-4 w-4 mt-0.5 mr-1 text-white"
            icon={faPlus}
          />
          Add Board
        </button>
      </div>

      {showModal ? (
        <div class="fixed z-10 inset-0 overflow-y-auto">
          <div class="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div class="fixed inset-0 transition-opacity" aria-hidden="true">
              <div class="absolute inset-0 bg-gray-500 opacity-75"></div>
            </div>

            <span
              class="hidden sm:inline-block sm:align-middle sm:h-screen"
              aria-hidden="true"
            >
              &#8203;
            </span>

            <div
              class="inline-block align-bottom bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full"
              role="dialog"
              aria-modal="true"
              aria-labelledby="modal-headline"
            >
              <div class="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                <div class="sm:flex sm:items-start">
                  <div class="mx-auto flex-shrink-0 flex items-center justify-center h-12 w-12 rounded-full bg-gray-100 sm:mx-0 sm:h-10 sm:w-10">
                    <FontAwesomeIcon
                      class="h-6 w-6 text-gray-600"
                      icon={faPlus}
                    />
                  </div>
                  <div class="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                    <h3
                      class="text-lg leading-6 font-medium text-gray-900"
                      id="modal-headline"
                    >
                      Create a board
                    </h3>
                    <div class="mt-2">
                      <p class="text-sm text-gray-500">
                        Insert the name of the board you want to create
                      </p>
                    </div>
                    <div class="mt-2">{formInput}</div>
                  </div>
                </div>
              </div>
              <div class="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
                <button
                  type="button"
                  onClick={createBoard}
                  class="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-green-600 text-base font-medium text-white hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 sm:ml-3 sm:w-auto sm:text-sm "
                >
                  Create
                </button>
                <button
                  type="button"
                  onClick={() => setShowModal(false)}
                  class="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm "
                >
                  Cancel
                </button>
              </div>
            </div>
          </div>
        </div>
      ) : null}
    </React.Fragment>
  );
};
export default CreateBoard;

