import "./App.css";
import initialData from "./initialData";
import React from "react";
import { useState, useEffect } from "react";
import styled from "styled-components";

import CreateBoard from "./components/createBoard";
import Board from "./board/boardsType/board";
import Grid from "./grid/grid";
import Navbar from "./Navbar";

const Header = styled.div`
  height: 30px;
  border-bottom: 1px solid #f3f4f6;
  padding-left: 10px;
  background-color: #4b5563
`;
const Container = styled.div``;
const Title = styled.h1`
  font-size: 25px;
  color: #fff;
`;

const getLstorage = () => {
  if (localStorage.getItem("bee")) {
    return JSON.parse(localStorage.getItem("bee"));
  } else {
    localStorage.setItem("bee", JSON.stringify(initialData));
    return JSON.parse(localStorage.getItem("bee"));
  }
};

function App() {
  document.body.style.backgroundColor = "#fff";
  const [boards, setBoards] = useState(getLstorage());

  const updateBoard = (boardName) => {
    let data = boards;
    let newBoardOrder = data.boardsOrder ? data.boardsOrder : [];
    newBoardOrder.push(boardName);
    let newData = {
      ...data,
      boardsOrder: newBoardOrder,
      boards: {
        ...data.boards,
        [boardName]: {
          type: "kanban",
          id: boardName,
          title: boardName,
          content: {
            tasks: {},
            columns: {},
            columnOrder: [],
          },
        },
      },
    };
    setBoards(newData);
  };

  useEffect(() => {
    localStorage.setItem("bee", JSON.stringify(boards));
  }, [boards]);

  return (
    <React.Fragment>
      <Navbar/>
        <CreateBoard onChange={updateBoard}></CreateBoard>
      <div>
        <div class="mx-auto">
          <Grid data={boards} ></Grid>
        </div>
      </div>

     
    </React.Fragment>
  );
}

export default App;
