import React, { useEffect } from "react";
import { Droppable, DragDropContext } from "react-beautiful-dnd";
import Bee from "./bee";
import styled from "styled-components";
import { useState } from "react";

const Container = styled.div``;

const Board = ({ data }) => {
  const [update, setUpdate] = useState(false);

  const saveBoard = (id, value) => {
    let newData = {
      ...data,
      boards: {
        ...data.boards,
        [id]: {
          ...data.boards[id],
          content: value,
        },
      },
    };
    localStorage.setItem("bee", JSON.stringify(newData));
    setUpdate((prev) => !prev);
  };

  const deleteBoard = (id) => {
    let newData = { ...data };
    let index = newData.boardsOrder.indexOf(id);
    newData.boardsOrder.splice(index, 1);
    delete newData.boards[id];
    localStorage.setItem("bee", JSON.stringify(newData));
    setUpdate((prev) => !prev);
  };

 const boardsList = data.boardsOrder.map((boardId, index) => {
    const board = data.boards[boardId];
    
    return (
        <Bee
            key={board.id + board.title}
            initialData={board.content}
            board={board}
            index={index}
            deleteBoard={deleteBoard}
            saveBoard={saveBoard}
        />
    )
  })

  return (
    <React.Fragment>
     {boardsList}
    </React.Fragment>
  );
};

export default Board;

