import "../../App.css";
import { useState, useEffect } from "react";
import Column from "../column";
import ComponentCode from "./componentCode";
import "@atlaskit/css-reset";
import styled from "styled-components";
import { DragDropContext, Draggable, Droppable } from "react-beautiful-dnd";
import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTrashAlt,
  faTerminal,
  faEllipsisH,
  faPlus,
  faCode,
} from "@fortawesome/free-solid-svg-icons";
import { last } from "lodash";

const ContainerKanban = styled.div`
  display: flex;
  margin-top: 5px;
  margin-bottom: 5px;
  border-top: 1px solid #f3f4f6;
`;

const ClonedDnd = styled.div`
  background-color: #f3f4f6 !important;
  border: 1px solid #d0d1d7;
  border-radius: 5px;
`;

function Bee(props) {
  const { initialData } = props;
  const [clmAvalaible, setClmAvalaible] = useState(1);
  const [clmName, setClmName] = useState("");
  const [data, setData] = useState(initialData);

  let onDragStart = () => {
    document.body.style.transition = "background-color 0.2s ease";
  };

  const onDragUpdate = (update) => {
    const { destination } = update;
    const opacity = destination
      ? destination.index / Object.keys(data.tasks).length
      : 0;
    document.body.style.backgroundColor = `rgba(153, 141, 217, ${opacity}`;
  };

  const onDragEnd = (result) => {
    document.body.style.color = "inherit";
    document.body.style.backgroundColor = "inherit";

    const { destination, source, draggableId, type } = result;

    if (!destination) {
      return;
    }

    if (
      destination.droppableId === source.droppableId &&
      destination.index === source.index
    ) {
      return;
    }

    if (type === "column") {
      const newColumnOrder = Array.from(data.columnOrder);
      newColumnOrder.splice(source.index, 1);
      newColumnOrder.splice(destination.index, 0, draggableId);

      const newData = {
        ...data,
        columnOrder: newColumnOrder,
      };
      setData(newData);
      return;
    }

    const start = data.columns[source.droppableId];
    const finish = data.columns[destination.droppableId];

    if (start === finish) {
      const newTaskIds = Array.from(start.taskIds);
      newTaskIds.splice(source.index, 1);
      newTaskIds.splice(destination.index, 0, draggableId);

      const newColumn = {
        ...start,
        taskIds: newTaskIds,
      };

      const newData = {
        ...data,
        columns: {
          ...initialData.columns,
          [newColumn.id]: newColumn,
        },
      };

      setData(newData);
    } else {
      // Moving from one list to another
      const startTaskIds = Array.from(start.taskIds);
      startTaskIds.splice(source.index, 1);
      const newStart = {
        ...start,
        taskIds: startTaskIds,
      };

      const finishTaskIds = Array.from(finish.taskIds);
      finishTaskIds.splice(destination.index, 0, draggableId);
      const newFinish = {
        ...finish,
        taskIds: finishTaskIds,
      };

      const newData = {
        ...data,
        columns: {
          ...data.columns,
          [newStart.id]: newStart,
          [newFinish.id]: newFinish,
        },
      };
      setData(newData);
    }
  };

  useEffect(() => {
    let newData = { ...data };

    if (
      clmAvalaible > 1 &&
      clmName.indexOf("ct -n") > -1 &&
      clmName.indexOf(" -in ") < 0
    ) {
      let newCommand = clmName;
      newCommand = newCommand.replace("ct -n", "");

      let listOfTask = newCommand.split(",");

      listOfTask.forEach((x) => {
        let id = x.trim();
        newData.tasks[id] = {
          id: id,
          content: x,
        };
        newData.columns[Object.keys(newData.columns)[0]].taskIds.push(id);
        setData(newData);
      });
    } else if (clmAvalaible > 1 && clmName.includes("rc -n")) {
      let newCommand = clmName;
      let clmToDelete = newCommand.replace("rc -n", "").trim().split(",");
      clmToDelete.forEach((x) => {
        let name = x.trim();
        if (newData.columnOrder.includes(name)) {
          newData.columnOrder.indexOf(name);
          newData.columnOrder.splice(newData.columnOrder.indexOf(name), 1);
          delete newData.columns[name];
          setData(newData);
        }
      });

      return;
    } else if (
      clmAvalaible > 1 &&
      clmName.indexOf("ct -n") > -1 &&
      clmName.indexOf(" -in ") > -1
    ) {
      let newCommand = clmName;
      newCommand = newCommand.replace("ct -n", "");

      let columnTask = newCommand.split(" -in ").pop();

      let listOfTask = newCommand.split(" -in ")[0];

      listOfTask.split(",").forEach((x) => {
        let id = x.trim();
        newData.tasks[id] = {
          id: id,
          content: x,
        };
        newData.columns[columnTask].taskIds.push(id);
        setData(newData);
      });
    } else if (clmAvalaible > 1 && clmName.indexOf("ct -n") > -1) {
      let newCommand = clmName;
      newCommand = newCommand.replace("ct -n", "");
      newCommand = newCommand.split(",");

      newCommand.forEach((x) => {
        let id = x.trim();
        newData.tasks[id] = {
          id: id,
          content: x,
        };

        newData.columns[Object.keys(newData.columns)[0]].taskIds.push(id);
        setData(newData);
      });
    }

    if (clmAvalaible > 1 && clmName.indexOf("rm") > -1) {
      let newCommand = clmName;

      if (newCommand.indexOf("*") > -1 && newCommand.indexOf("-e") > -1) {
      } else if (newCommand.indexOf("*") > -1) {
        for (let key of Object.keys(newData.columns)) {
          delete newData.columns[key];
        }
        newData.columnOrder = [];
        setData(newData);
      } else {
      }
    }

    if (clmAvalaible > 1 && clmName.indexOf("cc -n") > -1) {
      let newCommand = clmName;
      newCommand = newCommand.replace("cc -n", "");
      newCommand = newCommand.split(",");
      let clmId = clmAvalaible;

      newCommand.forEach((x) => {
        let id = x;
        id = id.trim();
        let newData = { ...data };
        newData.columns[id] = {
          id: id,
          title: x,
          taskIds: [],
        };
        newData.columnOrder.push(id);
        setData(newData);
        clmId += 1;
      });
    }
    setClmName("");
  }, [clmAvalaible]);

  useEffect(() => {
    props.saveBoard(props.board.id, data);
    //localStorage.setItem(props.board.id, JSON.stringify(data))
  }, [data]);

  const addColumn = () => {
    if (clmName.length > 0) {
      setClmAvalaible((prevCount) => prevCount + 1);
    }
  };

  const onCommandChange = (ev) => {
    const value = ev.target.value;
    setClmName(value);
  };

  function onExecSubmit(e) {
    e.preventDefault();
    addColumn();
  }

  const onDeleteBoard = () => {
    props.deleteBoard(props.board.id);
  };

  const [showTerm, setShowTerm] = useState(false);
  const onTerminalClick = () => {};

  const [displayCode, setDisplayCode] = useState(false);
  const showComponentCode = () => {
    setDisplayCode((prev) => !prev);
  };

  const clone = (
    <div class="flex-1 mr-2.5">
      <div class="lg:flex lg:items-center lg:justify-between">
        <div class="flex-1 min-w-0">
          <div class="mt-1 flex flex-col sm:flex-row sm:flex-wrap sm:mt-0 sm:space-x-3">
            <div class="mt-2 flex-grow items-center text-sm text-gray-500"></div>

            <div class="mt-2 flex items-center text-sm text-gray-500">
              <FontAwesomeIcon
                class="flex-shrink-0 h-3 w-3 text-gray-400 hover:text-gray-900 cursor-pointer"
                icon={faEllipsisH}
              />
            </div>
            <div class="mt-2 flex items-center text-sm text-gray-500">
              <FontAwesomeIcon
                class="flex-shrink-0 h-3 w-3 text-gray-400 hover:text-gray-900 cursor-pointer"
                icon={faPlus}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  return (
    <React.Fragment>
      <div class="flex flex-col mb-3 px-1 flex-grow">
        <div class="sticky top-0 pb-3 px-1 bg-white">
          <div class="flex-1 min-w-0 py-2 ">
            <h1 class="text-2xl font-bold leading-7 text-gray-900 sm:text-2xl sm:truncate capitalize Drageee cursor-move">
              {props.board.title}
            </h1>

            <div class="mt-1 flex flex-col sm:flex-row sm:flex-wrap sm:mt-0 sm:space-x-3">
              <form
                class="flex-grow flex rounded-md shadow-sm"
                onSubmit={onExecSubmit}
              >
                <span class="inline-flex items-center px-3 rounded-l-md border border-r-0 border-gray-300 bg-gray-50 text-gray-500 text-sm">
                  <FontAwesomeIcon
                    class="flex-shrink-0 h-3 w-3 text-gray-400 hover:text-gray-900 cursor-pointer"
                    icon={faTerminal}
                  />
                </span>
                <input
                  type="text"
                  name="company_website"
                  id="company_website"
                  class="focus:ring-indigo-500 border focus:border-indigo-500 flex-1 block w-full rounded-none rounded-r-md sm:text-sm border-gray-300 pl-2"
                  value={clmName}
                  onChange={onCommandChange}
                  placeholder="insert command here"
                />
              </form>

              <div class="mt-2 flex items-center text-sm text-gray-500">
                <FontAwesomeIcon
                  class="flex-shrink-0 h-3 w-3 text-gray-400 hover:text-gray-900 cursor-pointer"
                  icon={faCode}
                  onClick={showComponentCode}
                />
              </div>

              <div class="mt-2 flex items-center text-sm text-gray-500">
                <FontAwesomeIcon
                  class="flex-shrink-0 h-3 w-3 text-gray-400 hover:text-gray-900 cursor-pointer"
                  icon={faEllipsisH}
                />
              </div>

              <div class="mt-2 flex items-center text-sm text-gray-500">
                <FontAwesomeIcon
                  class="flex-shrink-0 h-3 w-3 text-gray-400 hover:text-gray-900 cursor-pointer"
                  icon={faTrashAlt}
                  onClick={onDeleteBoard}
                />
              </div>
            </div>
          </div>
        </div>

        <div >
          <div >
            {displayCode && <ComponentCode />}

            {!displayCode && (
              <DragDropContext
                onDragStart={onDragStart}
                onDragUpdated={onDragUpdate}
                onDragEnd={onDragEnd}
              >
                <Droppable
                  droppableId={"all-columns"}
                  direction={"horizontal"}
                  type={"column"}
                  renderClone={(provided, snapshot, rubric) => (
                    <ClonedDnd
                      {...provided.draggableProps}
                      {...provided.dragHandleProps}
                      ref={provided.innerRef}
                    >
                      {clone}
                    </ClonedDnd>
                  )}
                >
                  {(provided) => (
                    <div
                      className={"flex"}
                      {...provided.droppableProps}
                      ref={provided.innerRef}
                    >
                      {data.columnOrder.map((columnId, index) => {
                        const column = data.columns[columnId];
                        const tasks = column.taskIds.map(
                          (taskIds) => data.tasks[taskIds]
                        );

                        let last = data.columnOrder.length - 1 == index;

                        return (
                          <Column
                            key={column.id}
                            column={column}
                            tasks={tasks}
                            index={index}
                            last={last}
                          />
                        );
                      })}
                      {provided.placeholder}
                    </div>
                  )}
                </Droppable>
              </DragDropContext>
            )}
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default Bee;
