import React from "react";
import { useState, useEffect } from "react";
import JSONPretty from 'react-json-pretty';

const getSettings = () => {
  if (localStorage.getItem("bee")) {
    let data = localStorage.getItem("bee");
    return data;
  } else {
    return JSON.stringify({});
  }
};

const ComponentCode = (props) => {
  const [codeData, setCodeData] = useState(getSettings());

  useEffect(() => {

  }, []);

  return (
    <div class="overflow-y-auto">
        <JSONPretty class="p-5" id="json-pretty" data={codeData}></JSONPretty>
    </div>
  );
};

export default ComponentCode;
